.container {
  width: 80rem;
  max-width: 90%;
  min-height: 50rem;
  margin: 2rem auto;

  &__home {
    text-align: center;
    margin: 2rem;
    font-size: 2rem;
    color: #161743;

    cursor: pointer;
  }

  &__return {
    margin: 2rem;
    font-size: 2rem;
    color: rgb(85, 26, 139);

    cursor: pointer;
  }

  &__name {
    font-size: 5rem;

    margin: 2rem;
    border-bottom: 1px solid black;
  }
}

.card {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: flex-start;

  margin: 2rem;

  &__image {
    width: 30rem;
    border-radius: 1rem;
  }

  &__info {
    display: block;
    flex-direction: column;

    width: 44.5rem;
    border-radius: 1rem;
    overflow: auto;

    border: 1px solid #e0e0e0;

    li {
      font-size: 1.5rem;
      list-style-type: none;

      padding: 1.2rem;

      border: 1px solid #e0e0e0;
    }
  }
}
